(function ()
{
  'use strict';

  angular
    .module('app.got',
      [
        'flow','ngMdIcons','ngMessages','fuse','global','app.core'
      ]
    )
    .config(config)


  /** @ngInject */
  function config($stateProvider,$httpProvider,CONSTS,$translatePartialLoaderProvider, msApiProvider, msNavigationServiceProvider)
  {
    // State

    $stateProvider
      .state('app.got', {
        abstract: true,
        url     : '/got'
      })
      .state('app.got.battles', {
        url      : '/battles',
        views    : {
          'content@app': {
            templateUrl: 'app/main/apps/got/views/battles/battles.html',
            controller : 'BattlesController as vm'
          }
        },
        resolve  : {
          Battles: function (msApi)
          {
            return msApi.resolve('got.battles@get');
          }
        }
      })
      .state('app.got.battles.battle',{
        url   : '/detail',
        views : {
          'content@app':{
            templateUrl: 'app/main/apps/got/views/battle/battle.html',
            controller : 'BattleController as vm'
          }
        },
        params: {battle: null}
      })
      .state('app.got.stats',{
        url   : '/stats',
        views : {
          'content@app':{
            templateUrl: 'app/main/apps/got/views/stats/stats.html',
            controller : 'StatsController as vm'
          }
        },
        resolve  : {
          Stats: function (msApi)
          {
            return msApi.resolve('got.stats@get');
          }
        }
      })
    ;

    // Api

    msApiProvider.register('got.battles',['http://128.199.118.212:6543/api/battles']);
    msApiProvider.register('got.stats',['http://128.199.118.212:6543/api/stats']);

    // msNavigationServiceProvider

    msNavigationServiceProvider.saveItem('apps.got', {
      title : 'GOT',
      icon  : 'icon-tile-four',
      weight: 2
    });

    msNavigationServiceProvider.saveItem('apps.got.battles', {
      title: 'Battles',
      state: 'app.got.battles'
    });
    msNavigationServiceProvider.saveItem('apps.got.stats', {
      title: 'Stats',
      state: 'app.got.stats'
    });


    $httpProvider.defaults.headers.patch = {};
  }
})();

(function ()
{
  'use strict';

  angular
    .module('app.got')
    .controller('StatsController', StatsController);
  /** @ngInject */
  function StatsController($scope,$document,$timeout,$http,$state,$stateParams,msApi,CONSTS,Stats) {
    var vm = this;
    $scope.CONSTS = CONSTS;
    vm.stats ={};
    Stats.$promise.then(function (data) {
      vm.stats = data;
    });

  }


})();

(function ()
{
  'use strict';

  angular
    .module('app.got')
    .controller('BattlesController', BattlesController);
  /** @ngInject */
  function BattlesController($scope,$document,$timeout,$http,$state,$stateParams,Battles,msApi) {

    var vm = this;
    vm.Battles=[];
    vm.Battles_length = 0;
    getBattles();
    vm.gotobattleDetail = gotobattleDetail

    vm.dtInstance = {};
    vm.dtOptions = {
      dom         : 'rt<"bottom"<"left"<"length"l>><"right"<"info"i><"pagination"p>>>',

      initComplete: function ()
      {

        var api = this.api(),
          searchBox = angular.element('body').find('#got-battles-search');

        // Bind an external input as a table wide search box
        if ( searchBox.length > 0 )
        {
          searchBox.on('keyup', function (event)
          {
            api.search(event.target.value).draw();
          });
        }
      },
      pagingType  : 'simple',
      lengthMenu  : [10, 20, 30, 50, 100],
      pageLength  : 20,
      scrollY     : 'auto',
      responsive  : true
    };

    function gotobattleDetail($event,battle) {
      $event.stopPropagation();
      $state.go('app.got.battles.battle', {battle:battle});
    }
    function getBattles(params) {
      var result = msApi.resolve('got.battles@get',params);
      result.then(function (data) {
        vm.Battles_length = data.total;
        vm.Battles = data.data;
        console.log(vm.Battles);

      });
    }

  }


})();

(function ()
{
  'use strict';

  angular
    .module('app.got')
    .controller('BattleController', BattleController);
  /** @ngInject */
  function BattleController($scope,$document,$timeout,$http,$state,$stateParams,msApi,CONSTS) {
      var vm = this;
      $scope.CONSTS = CONSTS;
      vm.battle ={};
      vm.gotobattles = gotobattles;
      if($stateParams.battle==null){
        gotobattles();
        return;
      }
      else{
        vm.battle=$stateParams.battle;
        console.log(vm.battle);
      }
    function gotobattles() {
      $timeout(function () {
        $state.go('app.got.battles', {}, {reload: true, inherit: false, notify: true});
      }, 1000);

    }
  }


})();

